import { Controller, Block } from '@ali/4ever-cangjie';
import { toggleBlockquote } from '../../plugins/blockquote/actions';
import { createEditor } from './helpers';

describe('set blockquote in table', () => {
  it('set blockquote, clear quote', async () => {
    const { controller } = await createEditor({
      table: [
        ['A', 'A'],
        ['B', 'B'],
      ],
      tableSelection: {
        startColIndex: 0,
        startRowIndex: 0,
        endColIndex: 0,
        endRowIndex: 1,
      },
    });
    controller.run('onAction', toggleBlockquote());
    const getBlockquoteCount = (controller: Controller) => {
      return controller.value.document.filterDescendants(
        node => Block.isBlock(node) && node.data.blockquote
      ).length;
    };
    expect(getBlockquoteCount(controller)).toBe(2);

    controller.run('onAction', toggleBlockquote());
    expect(getBlockquoteCount(controller)).toBe(0);
  });
})